import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';

import { AppMenu } from './app-menu';
import { AppsMenuService } from './apps-menu.service';

@Component({
  selector: 'app-menu',
  templateUrl: './app-menu.component.html',
  styleUrls: ['./app-menu.component.css']
})
export class AppMenuComponent implements OnInit {
  apps: AppMenu[] = [];
  shouldPostMessage = false;

  // , 'LMS', 'Communication', 'Classroom', 'RWU', 'Cash', 'Medical'

  constructor(private _appsMenuService: AppsMenuService,
    private route: ActivatedRoute,
    private router: Router) {
  }

  ngOnInit() {
    this.apps = this._appsMenuService.getAppsMenu();
  }
}
